import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Timer } from "../../../component"
import $ from "jquery"

const FindId = () => {
    const navigate = useNavigate()
    const [disabled, setDisabled] = useState(true)
    const [timer, setTimer] = useState(false)
    const [email, setEmail] = useState("")
    const [phone, setPhone] = useState("")
    const [number, setNumber] = useState("")
    const emailRegex =
        /([\w-.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/

    const sendingMessage = () => {
        $.ajax({
            async: true, type: 'POST',
            url: "https://dev.odoc-api.com/sms_auth/send",
            data: { "phone_number": phone }, dataType: 'json',
            success: (response) => {
                if (response.result === "SEND") {
                    setTimer(true);
                    document.getElementById("error_1").className = "t_error hidden";
                    document.getElementById("error_2").className = "t_error hidden";
                    alert(phone + "  .\n 5 .");
                }
            },
            error: (response) => {
                setTimer(false)
                document.getElementById("error_1").className = "t_error"
                alert('    .')
            }
        });
    }

    const verification = () => {
        $.ajax({
            async: true, type: 'GET',
            url: "https://dev.odoc-api.com/sms_auth/verification?phone_number=" + phone + "&auth_number=" + number,
            dataType: 'json',
            success: (response) => {
                if (response.result === "SUCCESS") {
                    alert(" .")
                    sessionStorage.setItem("phone_certification", true)
                    document.getElementById("error_2").className = "t_error hidden"
                    document.getElementById("comp_1").className = "t_comp"
                    setDisabled(false)
                    setTimer(false)
                }
                else alert(" .")
            },
            error: (response) => {
                document.getElementById("error_2").className = "t_error"
                alert(" .     .")
            }
        });
    }

    const sendResetMail = () =>{
        $.ajax({
            async:false, type:'POST',
            url: "https://api.odoc-api.com/rest_auth/password/reset/",
            data : { 
                "email" : email 
            }, dataType: 'json',
            success: (response) => {
                alert("   .\n  .")
                navigate("/login")
                console.log(response)
            },
            error: (response) => console.log(response)
        })
    }

    return (
        <div>
            <header id="header" className="header">
                <div className="inr-c">
                    <h2 className="hidden">/</h2>
                    <div className="lft">
                        <button type="button" className="btn-back c-white" onClick={() => navigate(-1)}>
                            <span className="i-aft i_back"></span>
                        </button>
                    </div>
                </div>
            </header>

            <div id="container" className="container sub member">
                <div className="inr-c">
                    <div className="area_member">
                        <h2 className="h_tit3">/  <strong> </strong></h2>
                        <div className="box-inp">
                            <div>
                                <label htmlFor="id">   </label>
                                <div className="ip">
                                    <input type="text" id="id" placeholder="    " 
                                        value={email} onChange={(e)=>{
                                            document.getElementById("error_3").className = "hidden"
                                            setEmail(e.target.value)
                                        }}
                                        disabled={!disabled}/>
                                </div>
                                <p id="error_3" className="t_error hidden">   </p>
                            </div>
                            <div>
                                <label htmlFor="phone">   </label>
                                <div className="ip">
                                    <input type="text" id="phone" placeholder="' - '   " 
                                        value={phone} onChange={(e) => {
                                            document.getElementById("error_1").className = "hidden"
                                            setPhone(e.target.value)
                                        }}
                                        disabled={!disabled} />
                                    <div>
                                        <button type="button" className="btn-pk ss blue2 bdrs"
                                            onClick={()=>{
                                                if(emailRegex.test(email)){
                                                    document.getElementById("error_3").className = "hidden"
                                                    setTimer(false)
                                                    sendingMessage()
                                                }else{
                                                    alert("   .")
                                                    document.getElementById("error_3").className = "t_error"
                                                }
                                                
                                            }}>
                                            <span></span>
                                        </button>
                                    </div>
                                </div>
                                <p id="error_1" className="t_error hidden">   </p>
                            </div>
                            {timer ? <Timer minute={5} second={0} setTimer={setTimer} /> : <></>}
                            <div>
                                <label htmlFor="number"></label>
                                <div className="ip">
                                    <input type="text" id="number" placeholder="  " 
                                        value={number} onChange={(e)=>{setNumber(e.target.value)}}
                                        disabled={!disabled}/>
                                    <div>
                                        <button type="button" onClick={verification} className="btn-pk ss blue2 bdrs">
                                            <span></span>
                                        </button>
                                    </div>
                                </div>
                                <p id="error_2" className="t_error hidden">   </p>
                                <p id="comp_1" className="t_comp hidden"> </p>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="fix_botm">
                    <button type="button" className="btn-pk blue n" disabled={disabled}
                        onClick={() =>{
                            sendResetMail();
                        }}>
                        <span></span>
                    </button>
                </div>
            </div>
        </div>
    )
}

export default FindId;