import React, { useEffect } from 'react';
import { Howl } from 'howler';

const WarningSound = () => {
    useEffect(() => {
        const soundURL = '/assets/alarm.mp3';
    
        const sound = new Howl({
          src: [soundURL],
          volume: 1,
        });
    
        sound.play();
    
        //      .
        return () => {
          sound.stop();
        };
      }, []);
    

  return (
    <div>
      {/*   */}
      <p></p>
    </div>
  );
};

export default WarningSound;