import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import $ from "jquery";

const SignUp = () => {
    const navigate = useNavigate();
    const [email1, setEmail1] = useState("");
    const [email2, setEmail2] = useState("");
    const [password1, setPassword1] = useState("");
    const [password2, setPassword2] = useState("");
    const [gender, setGender] = useState("");
    const [region, setRegion] = useState("");
    const [age, setAge] = useState("");
    const [disabled, setDisabled] = useState(false);
    const emailRegex =
        /([\w-.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/

    useEffect(() => {
        if (email1 !== "" && email2 !== "") {
            document.getElementById("emailerror1").className = "hidden";
            document.getElementById("email_in_use").className = "hidden";
        }
    }, [email1, email2]);

    useEffect(() => {
        onChangePassword();
    }, [password1, password2]);

    const onChangePassword = () => {
        document.getElementById('pwderror2').className = "hidden";
        document.getElementById('pwderror3').className = "hidden";
        document.getElementById('pwdok').className = "hidden";
        if (password1.length && (password1.length > 20 || password1.length < 8)) {
            document.getElementById('pwdlengtherror').className = "t_error";
            document.getElementById('pwderror5').className = "hidden";
        } else {
            document.getElementById('pwdlengtherror').className = "hidden";
            if (password2.length === 0) {
                document.getElementById('pwderror5').className = "hidden";
            } else if (password1 !== password2) {
                document.getElementById('pwderror5').className = "t_error";
            } else {
                document.getElementById('pwderror5').className = "hidden";
                document.getElementById('pwdok').className = "t_comp";
            }
        }
    };

    const fieldCheck = () => {
        let flag = false;
        document.getElementById("email_in_use").className = "hidden";
        document.getElementById("emailerror1").className = "hidden";
        document.getElementById("emailerror2").className = "hidden";
        document.getElementById("pwdlengtherror").className = "hidden";
        document.getElementById("pwderror1").className = "hidden";
        document.getElementById("pwderror2").className = "hidden";
        document.getElementById("pwderror3").className = "hidden";
        document.getElementById("pwderror4").className = "hidden";
        document.getElementById("pwderror5").className = "hidden";
        document.getElementById("gendererror1").className = "hidden";
        document.getElementById("regionerror1").className = "hidden";
        if (email1 === "" || email2 === "") {
            document.getElementById("emailerror1").className = "t_error";
            flag = true;
        } else if (!emailRegex.test(email1 + '@' + email2)) {
            document.getElementById("emailerror2").className = "t_error";
            flag = true;
        }
        if (password1 !== password2) {
            document.getElementById("pwderror4").className = "t_error";
            flag = true;
        }
        if (gender === "") {
            document.getElementById("gendererror1").className = "t_error";
            flag = true;
        }
        if (region === "") {
            document.getElementById("regionerror1").className = "t_error";
            flag = true;
        }
        if (flag) return false;
        else return true;
    };

    const register = () => {
        $.ajax({
            async: true, type: 'POST',
            url: "https://dev.odoc-api.com/member/register/",
            data: {
                "email": email1 + "@" + email2,
                "password": password1,
                "gender": gender, "region": region, "age": age,
            }, dataType: 'json',
            success: (response) => {
                alert(" .");
                if (response.user.email === email1 + "@" + email2)
                    navigate("/login");
            },
            error: (response) => {
                if (response.status === 500) {
                    alert("  .");
                    document.getElementById("email_in_use").className = "t_error";
                    return;
                }
                const text = JSON.parse(response.responseText);
                if (text.password1) {
                    if (text.password1[0] === "   .") {
                        document.getElementById('pwderror2').className = "t_error";
                        document.getElementById('pwdok').className = "hidden";
                    } else if (text.password1[0] === "    .") {
                        document.getElementById('pwderror3').className = "t_error";
                        document.getElementById('pwdok').className = "hidden";
                    }
                }
            }
        });
        sessionStorage.removeItem("phone_certification");
        sessionStorage.removeItem("policy_checked");
    };

    return (
        <div>
            <header id="header" className="header">
                <div className="inr-c">
                    <h2 className="tit"></h2>
                    <div className="lft">
                        <button type="button" className="btn-back c-white" onClick={() => navigate(-1)}>
                            <span className="i-aft i_back"></span>
                        </button>
                    </div>
                </div>
            </header>
            <div id="container" className="container sub member">
                <div className="inr-c">
                    <div className="area_member">
                        <h2 className="h_tit2 mb40">   </h2>
                        <div className="box-inp">
                            <div>
                                <label htmlFor="email"></label>
                                <div className="inp_email">
                                    <input type="text" className="inp_txt" value={email1}
                                        onChange={(e) => {
                                            setEmail1(e.target.value);
                                        }} />
                                    <span>@</span>
                                    <input type="text" className="inp_txt" value={email2} disabled={disabled}
                                        onChange={(e) => {
                                            setEmail2(e.target.value);
                                        }} />
                                    <select className="select1"
                                        onChange={(e) => {
                                            if (e.target.value !== "") setDisabled(true);
                                            else setDisabled(false);
                                            setEmail2(e.target.value);
                                        }}>
                                        <option value=""></option>
                                        <option value="naver.com"></option>
                                        <option value="hanmail.net"></option>
                                        <option value="nate.com"></option>
                                        <option value="gmail.com"></option>
                                        <option value="kakao.com"></option>
                                    </select>
                                </div>
                                <p id="email_in_use" className="t_error hidden">  </p>
                                <p id="emailerror1" className="t_error hidden"> </p>
                                <p id="emailerror2" className="t_error hidden">  .</p>
                            </div>
                            <div>
                                <label htmlFor="password"></label>
                                <input type="password" placeholder="8~20  ,  "
                                    onChange={(e) => {
                                        document.getElementById("pwderror1").className = "hidden";
                                        setPassword1(e.target.value);
                                    }} />
                                <p id="pwdlengtherror" className="t_error hidden">8~20  ,  </p>
                                <p id="pwderror1" className="t_error hidden"> </p>
                                <p id="pwderror2" className="t_error hidden">   </p>
                                <p id="pwderror3" className="t_error hidden">    </p>
                            </div>
                            <div>
                                <input type="password" placeholder="  "
                                    onChange={(e) => {
                                        document.getElementById("pwderror4").className = "hidden";
                                        setPassword2(e.target.value);
                                    }} />
                                <p id="pwderror4" className="t_error hidden">  </p>
                                <p id="pwderror5" className="t_error hidden">  </p>
                                <p id="pwdok" className="t_comp hidden"> </p>
                            </div>

                            <div className="inp_gender_region">
                                <div>
                                    <label htmlFor="gender"></label>
                                    <input type="hidden" />
                                    <select className="select1"
                                        onChange={(e) => {
                                            document.getElementById("gendererror1").className = "hidden";
                                            setGender(e.target.value);
                                        }}>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                    </select>
                                    <p id="gendererror1" className="t_error hidden"> </p>
                                </div>
                                <span /> {/* margin */}
                                <div>
                                    <label htmlFor="region"></label>
                                    <input type="hidden" />
                                    <select className="select1"
                                        onChange={(e) => {
                                            document.getElementById("regionerror1").className = "hidden";
                                            setRegion(e.target.value);
                                        }}>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                    </select>
                                    <p id="regionerror1" className="t_error hidden"> </p>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
                <div className="fix_botm">
                    <button type="button" className="btn-pk blue n"
                        onClick={() => {
                            if (fieldCheck() == false) return;
                            alert(" .");
                            navigate("/login");//register();
                        }}>
                        <span></span>
                    </button>
                </div>
            </div>
        </div>
    );
};

export default SignUp;
