import { useRef } from 'react';

const PushNotification = () => {
  const notificationRef = useRef(null);

  // Notification       ,   Early return  .
  if (!Notification) {
    console.error('Notification   ');
    return;
  }
  
  // //       ,
  // if (Notification.permission !== 'granted') {  	
  //   // Chrome -     ,   return!
  //     try {
  //       Notification.requestPermission().then((permission) => {
  //         if (permission !== 'granted') return;  
  //       });
  //     } catch (error) {
  //       // Safari -     ,   return!
  //       if (error instanceof TypeError) {
  //         Notification.requestPermission().then((permission) => {
  //           if (permission !== 'granted') return;
  //         }); 
  //       } else {
  //       	console.error(error)
  //       }
  //     }
  //   }
    
    //    ,     
    const setNotificationClickEvent = () => {
      notificationRef.current.onclick = (event) => {
        event.preventDefault();
        window.focus();
        notificationRef.current.close();
      };
    };
  
  const fireNotification = () => {
    const title = ''; // Define the 'title' variable
    const newOption = {
      badge: '../assets/images/caution2.png',
      icon: '../assets/images/caution2.png',
      // ...options
    }
    
    // notificationRef Notification .      .
    notificationRef.current = new Notification(title , newOption)

    //     
    setNotificationClickEvent(); 
  }
  
  return { fireNotification }
}

export default PushNotification;